.image {
    width: 100%;
    min-height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: relative;
}

.image__head {
    width: 100%;
    min-height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 120px 0 50px;
}

.image__button {
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.image__arrow {
    width: 40px;
    height: 40px;
}

.image__ellipse_orange{
    @ellipse4();
    position: absolute;
    width: 600px;
    height: 600px;
    left: -300px;
    top: 150px;
    border-radius: 50%;
    z-index: 0;
    animation-name: verticalMove;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-timing-function: ease-in-out;
}

.image__ellipse_pink{
    @ellipse5();
    position: absolute;
    width: 800px;
    height: 1000px;
    right: -500px;
    bottom: -400px;
    border-radius: 20%;
    z-index: 0;
    animation-name: scale;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-timing-function: linear;
}

.image__title {
    font-family: Poppins, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 150%;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    color: #fff;
    margin: 0;
}

.image__content {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.image__description {
    max-width: 445px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 50px;
}

.image__subtitle {
    font-family: SF Pro Display, sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    text-transform: uppercase;
    color: #1EA6D1;
    margin: 0 0 10px 0;
}

.image__name {
    font-family: SF Pro Display, sans-serif;
    font-weight: 900;
    font-size: 25px;
    line-height: 120%;
    text-transform: uppercase;
    color: #fff;
    margin: 0 0 48px 0;
}

.image__text {
    font-family: SF Pro Display, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #fff;
    margin: 0 0 50px 0;
}